<template>
	<div>
		<div class="y-breadcrumb-box">
			<el-breadcrumb separator="/"> 
			  <el-breadcrumb-item :to="{ path: '/Index' }"></el-breadcrumb-item>
			  <el-breadcrumb-item></el-breadcrumb-item> 
			  <el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="y-row-box">
			<div class="y-inner">  
				<!--  begin -->
				<BaseInfo :listdata="initdata" :editabled="true" @save="saveBaseInfo"></BaseInfo> 
				<!--  end -->
				<!--  begin -->
				<InfoList 
					title='<i class="iconfont icon-mycenter text-yellow"></i>'
					:listdata="jobListData" 
					:editabled="true"  
					:theaddata='jobTheadData'
					@add="ShowAddJob = true"
					@delete="deleteJobInfo"
					@save="saveJobInfo"></InfoList> 
				<!--  end -->
				<!--  begin -->
				<InfoList 
					title='<i class="iconfont icon-mycenter text-yellow"></i>'
					:listdata="eduListData" 
					:editabled="true"  
					:theaddata='eduTheadData'
					@add="ShowAddEdu = true"
					@delete="deleteEduInfo"
					@save="saveEduInfo"></InfoList> 
				<!--  end -->
				<!--  begin -->
				<InfoList 
					title='<i class="iconfont icon-mycenter text-yellow"></i>'
					:editabled="false"  
					:listdata="peixunListData"  
					:theaddata='peixunTheadData'></InfoList> 
				<!--  end -->
			</div> 
		</div>
		<AddJob :VisibleFlag="ShowAddJob" @close="ShowAddJob = false" @submit="addJobInfo"></AddJob>  
		<AddEdu :VisibleFlag="ShowAddEdu" @close="ShowAddEdu = false" @submit="addEduInfo"></AddEdu>  
	</div>
</template>
<script>
import {formatDate} from '@/assets/js/date.js';
import BaseInfo from './components/baseInfo';
import InfoList from './components/infoList';

import AddJob from './components/addJob'; //
import AddEdu from './components/addEdu'; //

export default {
	data() {
		return {   
	        //
	        ShowAddJob: false,
	        //
	        ShowAddEdu: false,
			initdata: [{
				id: 'name',
				text: '',
				val: '',
				edit: false
			},{
				id: 'sex',
				text: '',
				val: '',
				edit: false
			},{
				id: 'date',
				text: '',
				val: '1990-6-18',
				edit: false
			},{
				id: 'mail',
				text: '',
				val: 'zhangsan@yonyou.com',
				edit: true
			},{
				id: 'phone',
				text: '',
				val: '187xxxxxxxx',
				edit: true
			}],   
	        jobListData: [{
				date: '2016-05-02',
				danwei: '1',
				zhiwu: '1',
				zhize: 'aa'
			}],
			jobTheadData: [{
				param: 'date',
				text: '',
				required: true
			},{
				param: 'danwei',
				text: '',
				required: true
			},{
				param: 'zhiwu',
				text: '' 
			},{
				param: 'zhize',
				text: '' 
			}],
			eduListData: [{
				date: '2016-05-02',
				school: '1',
				zhuanye: '1',
				xueli: 'aa',
				xuewei: 'aa'
			}],
			eduTheadData: [{
				param: 'date',
				text: '',
				required: true
			},{
				param: 'school',
				text: '',
				required: true
			},{
				param: 'zhuanye',
				text: '',
				required: true 
			},{
				param: 'xueli',
				text: '',
				required: true 
			},{
				param: 'xuewei',
				text: '' 
			}],
			peixunListData: [{
				date: '2016-05-02',
				huodong: '1',
				xueshi: '1',
				danwei: 'aa',
				mubiao: 'aa',
				neirong: 'aa',
				chengji: 'aa'
			}],
			peixunTheadData: [{
				param: 'date',
				text: '' 
			},{
				param: 'huodong',
				text: '' 
			},{
				param: 'xueshi',
				text: '' 
			},{
				param: 'danwei',
				text: '' 
			},{
				param: 'mubiao',
				text: '' 
			},{
				param: 'neirong',
				text: '' 
			},{
				param: 'chengji',
				text: '' 
			}]  
		}
	},
	components: {
		BaseInfo, //
		InfoList, // 
		AddJob, //
		AddEdu, //
	}, 
	methods: { 
		//-[]
		saveBaseInfo( data ){ 
			console.info( 'saveBaseInfo', data);  
		},
		//-[]
		addJobInfo( data ){
			var _sdate = formatDate( data.date[0], 'yyyy-MM-dd');
			var _edate = formatDate(data.date[1], 'yyyy-MM-dd'); 
			data.date = _sdate + '' + _edate;   

			this.ShowAddJob = false;   
			this.jobListData.push( JSON.parse(JSON.stringify( data )) ); 
		},
		//-[]
		deleteJobInfo(index, row) {  
			this.jobListData.splice(index, 1);
		},
		//-[,]
		saveJobInfo(){
			alert('')
		},
		//-[]
		addEduInfo( data ){
			var _sdate = formatDate( data.date[0], 'yyyy-MM-dd');
			var _edate = formatDate(data.date[1], 'yyyy-MM-dd'); 
			data.date = _sdate + '' + _edate;   

			this.ShowAddEdu = false;   
			this.eduListData.push( JSON.parse(JSON.stringify( data )) ); 
		},
		//-[]
		deleteEduInfo(index, row) {  
			this.eduListData.splice(index, 1);
		},
		//-[,]
		saveEduInfo(){
			alert('')
		}  
	}
}
</script> 


