<template>
	<div>
		<div class="y-breadcrumb-box">
			<el-breadcrumb separator="/"> 
			  <el-breadcrumb-item :to="{ path: '/Index' }"></el-breadcrumb-item>
			  <el-breadcrumb-item></el-breadcrumb-item> 
			  <el-breadcrumb-item>{{id}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="y-row-box">
			<div class="y-inner">  
				<!--  begin -->
				<BaseInfo :listdata="initdata" :editabled="false"></BaseInfo> 
				<!--  end -->
				<!--  begin -->
				<InfoList 
					title='<i class="iconfont icon-mycenter text-yellow"></i>'
					:editabled="false"  
					:listdata="jobListData"  
					:theaddata='jobTheadData'></InfoList> 
				<!--  end -->
			</div>
		</div>
	</div>
</template>
<script>    
import BaseInfo from '@/components/query/myinformation/components/baseInfo';
import InfoList from '@/components/query/myinformation/components/infoList';
export default {
	data() {
		return { 
			initdata: [{
				id: 'name',
				text: '',
				val: '',
				edit: false
			},{
				id: 'sex',
				text: '',
				val: '',
				edit: false
			},{
				id: 'date',
				text: '',
				val: '1990-6-18',
				edit: false
			},{
				id: 'mail',
				text: '',
				val: 'zhangsan@yonyou.com',
				edit: true
			},{
				id: 'phone',
				text: '',
				val: '187xxxxxxxx',
				edit: true
			}],
			jobListData: [{
				date: '2016-05-02',
				danwei: '1',
				zhiwu: '1',
				zhize: 'aa'
			}],
			jobTheadData: [{
				param: 'date',
				text: '',
				required: true
			},{
				param: 'danwei',
				text: '',
				required: true
			},{
				param: 'zhiwu',
				text: '' 
			},{
				param: 'zhize',
				text: '' 
			}],
		}
	},
	mounted(){  
	},
	computed: {
		id(){
			return this.$route.params.id;
		}
	},
	components: { 
		BaseInfo,
		InfoList
	}, 
	methods: {  
	}
}
</script> 


